import Vue from 'vue'
import App from './App.vue'
import router from './router'
import {Input, Button, Select, Option,
   Tooltip, InfiniteScroll, MessageBox, Dialog, Dropdown, DropdownMenu, DropdownItem} from 'element-ui'

Vue.config.productionTip = false

Vue.use(Input).use(Button).use(Select)
.use(Option).use(Tooltip).use(InfiniteScroll).use(Dialog).use(Dropdown).use(DropdownMenu).use(DropdownItem)

Vue.prototype.$prompt = MessageBox.prompt

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
